.catalog {
  margin-bottom: 133px;

  &__content {
    margin-top: 43px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
  }

  &__item {
    border: 1px solid #eaeaea;
    max-width: 302px;
    width: 100%;
    transition: all .3s ease-in-out;

    img {
      height: 234px;
      display: block;
      margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 9px;
    }

    &:hover {
      box-shadow: 0 0 30px 0 rgba(255, 201, 79, 0.3);

      .catalog__btn {
        background: #fff450;
        color: #000;
      }

      .catalog__price {
        color: #cf0953;
      }

      svg {
        path {
          fill: #cf0953;
        }
      }
    }
  }

  &__inner {
    padding: 0 22px;

  }

  &__name {
    margin: 0;
    margin-bottom: 13px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 14px;
    line-height: 157%;
    color: #535353;
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 14px;
    line-height: 150%;
    color: #8c8c8c;
  }

  &__price {
    margin: 0;
    margin-bottom: 27px;
    display: flex;
    gap: 10px;
    align-items: baseline;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 105%;
    color: #535353;
    transition: all .3s ease-in-out;

    svg {
      path {
        transition: all .3s ease-in-out;
      }
    }
  }

  &__btn {
    width: 100%;
    padding: 19px;
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 14px;
    color: #fff;
    background: #3d4f64;
    transition: all .3s ease-in-out;
  }
}
